import { Component, ElementRef, ViewChild, OnInit } from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';

import { Observable } from 'rxjs';
import { concatMap, first } from 'rxjs/operators';

import { Ancillary, CompanyAncillary } from '@model';
import { AncillaryHelperService } from './ancillary-helper.service';
import { SessionService } from '@session';

@Component({
  selector: 'sp-ancillary-modal',
  templateUrl: './ancillary-modal.component.html'
})
/**
 * Modal Address Editor
 */
export class AncillaryModalComponent implements OnInit {
  private modalRef: NgbModalRef;
  /** Snapshot of the current company from a terminating observable. No need to unsubscribe. */
  ancillaryVm: Ancillary[];
  companyAncillaryVm: CompanyAncillary[];
  companyId: string;
  ancillaryIdListSelected: string[] = [];
  ancillaryViewModel$: Observable<Ancillary[]>;
  states: {[id: string]: string } = {};

  @ViewChild('ancillaryModal') ancillaryModal: ElementRef;

  constructor(
    private modalService: NgbModal,
    private sessionService: SessionService,
    private ancillaryHelperService: AncillaryHelperService,
    private sanitizer: DomSanitizer
  ) {}

  ngOnInit() {
    this.ancillaryViewModel$ = this.ancillaryHelperService.companyAncillaryVm$.pipe(
      first(),
      concatMap(vm => {
        this.companyAncillaryVm = vm;
        if (this.companyAncillaryVm === undefined || this.companyAncillaryVm.length === 0) {
          return this.ancillaryHelperService.getAncillaryData();
        }
      })
    );
    this.ancillaryViewModel$.pipe(first()).subscribe(vm1 => {
      this.ancillaryVm = vm1;
    });

    this.sessionService.currentCompanyId$.pipe(first()).subscribe(c => {
      this.companyId = c;
    });
  }

  private openModalDialog() {
    this.modalRef = this.modalService.open(this.ancillaryModal, { backdrop: 'static' });
    this.modalRef.result.then(res => {
      this.close(res);
    });
  }
  close(reason: string) {
    if (reason === 'send-me') {
      this.save();
    }
  }

  private htmlCache: any = {};
  public cacheHtml(anc: Ancillary): SafeHtml {
    // Need to cache the HTML. Due too much changes driven by the observables
    // breaks in IE11. On the HTML is calculated, is not going to change for a given ID
    if (!this.htmlCache[anc.id]) {
      const html = <any> this.getSafeHtml(anc);
      if (html.changingThisBreaksApplicationSecurity) {
        this.htmlCache[anc.id] = html;
      }
    }
    return this.htmlCache[anc.id] as SafeHtml;
  }
  public getSafeHtml(anc: Ancillary): SafeHtml {
    // Security: `ancillary.htmlVal` is always authored by the documentation team
    // and is considered to be safe.
    return this.sanitizer.bypassSecurityTrustHtml(anc.htmlVal);
  }
  private save() {
    this.ancillaryHelperService.saveCompanyAncillary(
      this.ancillaryIdListSelected,
      this.companyId
    );
  }

  checkAncillaryResponse() {
    if (this.companyAncillaryVm === undefined || this.companyAncillaryVm.length === 0) {
      this.openModalDialog();
    }
  }

  updateSelectedIds(itemId: string) {
    const state = this.states[itemId];
    if (state === 'checked') {
      this.states[itemId] = 'unchecked';
      const index: number = this.ancillaryIdListSelected.indexOf(itemId);
      if (index !== -1) {
        this.ancillaryIdListSelected.splice(index, 1);
      }
    } else {
      this.states[itemId] = 'checked';
      this.ancillaryIdListSelected.push(itemId);
    }
  }

  closeModal() {
    this.modalRef.close('ok');
  }
}
