import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { EsignEnvelopeResponse, EsignService } from '@app/core/services/esign.service';
import { DocumentPackage, DocumentPackageService } from '@entity/entity-services/document-package.service';
import { WellKnownRoutes } from '@core';
import { CompanyService, DocumentService, EnvelopeSignerService, HouseholdFilersService } from '@entity';
import { AccountStatus, Document, EnvelopeSigner, EnvelopeStatus, HouseholdFilers } from '@model';
import { SessionService } from '@session';
import { combineLatest, Observable, of } from 'rxjs';
import { concatMap, map, shareReplay } from 'rxjs/operators';
import { DocumentsVm } from './documents-vm';

@Injectable({providedIn: 'root'})
export class DocumentsViewService {
  /** Boolean Observable emits true if the user is an administrator. */
  isAdmin$: Observable<boolean>;

  hhFilers$: Observable<HouseholdFilers>;

  /** Observable array of Documents ViewModels. */
  vms$: Observable<DocumentsVm[]>;

  constructor (
    private documentService: DocumentService,
    private documentPackageService: DocumentPackageService,
    private envelopeSignerService: EnvelopeSignerService,
    private esignService: EsignService,
    private companyService: CompanyService,
    private router: Router,
    private sessionService: SessionService,
    private hhFilersService: HouseholdFilersService
    ) {
    this.isAdmin$ = sessionService.isAdmin$;
    this.hhFilers$ = combineLatest(
      sessionService.currentCompany$,
      hhFilersService.entities$).pipe(map(([co, hh]) => {
        return co.isHousehold ? hh.length && hh[0] : null;
      }));
    this.initializeVm$();
  }

  private initializeVm$() {
    this.vms$ = combineLatest(
      this.documentPackageService.documentPackage$,
      this.sessionService.currentUser$,
      this.sessionService.currentCompany$,
      this.hhFilers$
    ).pipe(
      map(([docpackage, user, currentCompany, hh]) => {
        const allDocs = docpackage.Document;
        const envelopes = docpackage.Envelope;
        const envUsers = docpackage.EnvelopeSigner;

        const activeEnvelopes = envelopes.filter(e => e && e.status !== EnvelopeStatus.voided);
        const hasVoidedEnvelope = activeEnvelopes.length < envelopes.length;
        const vms = activeEnvelopes.map(e => {
          const docs = allDocs.filter(d => d.envelopeId === e.id);
          const signers = envUsers.filter(eu => eu.envelopeId === e.id && !eu.isRemoved);
          const userSigner = user && signers.find(eu => eu.userId === user.id);
          const firstSigner = signers.find(s => true);

          const hhPrimarySigner = hh ? signers.find(s => s.firstName === hh.primaryFirstName && s.lastName === hh.primaryLastName) :
            firstSigner;
          const hhSpouseSigner = hh && signers.find(s => s.firstName === hh.spouseFirstName && s.lastName === hh.spouseLastName);

          let isSigner =
            !userSigner ? '' :
            userSigner.role === 'viewer' ? 'no' :
            userSigner.role === 'signer' && userSigner.isEmbedded === true ? 'yes' :
            '';
          if (isSigner === 'yes' && hh != null) {
            isSigner = (userSigner.firstName === hh.spouseFirstName && userSigner.lastName === hh.spouseLastName) ? 'spouse' : 'primary';
          }

          const isSigningComplete = e.status === EnvelopeStatus.completed;
          const isSent = isSigningComplete || e.status === EnvelopeStatus.sent;
          const isResign = hasVoidedEnvelope && !isSigningComplete;
          const isSignedByUser = userSigner && userSigner.status === EnvelopeStatus.completed;
          return {
            docs,
            envelope: e,
            signers,
            user,
            isSigner,
            isSigningComplete,
            isSignedByUser,
            isSent,
            isResign,
            signerEmail: hhPrimarySigner ? hhPrimarySigner.email : '',
            signerFirstName: hhPrimarySigner ? hhPrimarySigner.firstName : '',
            signerLastName: hhPrimarySigner ? hhPrimarySigner.lastName : '',
            signerTitle: firstSigner ? firstSigner.title : '',
            spouseEmail: hhSpouseSigner ? hhSpouseSigner.email : '',
            spouseFirstName: hhSpouseSigner ? hhSpouseSigner.firstName : '',
            spouseLastName: hhSpouseSigner ? hhSpouseSigner.lastName : '',
            companyStatus: currentCompany ? currentCompany.status : ''
          } as DocumentsVm;
        });
        vms.sort((a, b) => a.envelope.isWetSign ? 1 : -1);
        return vms;
      }),
      concatMap(vms => this.navigateIfSigningComplete(vms)),
      shareReplay(1)
    );
  }

  /**
   * Get the PDF for a signable document by its id.
   * @param docId Id of the PDF to download
   * @returns The PDF as a Blob
   */
  getDocPdf(doc: Document): Observable<Blob> {
    return this.documentService.getDocPdf(doc.id);
  }

  /**
   * Get a temporary Esign URL which enables short-term access to an ESign envelope
   * @return Terminating observable emitting the URL
   */
  getEsignEnvelopeUrl(eSignEnvelopeId: string): Observable<string> {
    return this.esignService.getEnvelopeUrl(eSignEnvelopeId);
  }

  private navigateIfSigningComplete(vms: DocumentsVm[]): Observable<DocumentsVm[]> {
    if (vms.length > 0 && vms.every(v => v.isSigningComplete || v.envelope.isWetSign)) {
      // Have docs and all of them are signed.
      // Evidently the Company status is not up-to-date because we shouldn't be here.
      // When all documents are signed, the AccountStatus should be "Operations"
      // and the app should be showing the "Processing" page.
      // Reload the Company and navigate to "Processing" page
      const status = vms[0].companyStatus;
      if (status === AccountStatus.AutoMigrate || status === AccountStatus.Operations || status === AccountStatus.Migrating) {
        this.router.navigate([WellKnownRoutes.Processing]);
        return of(null); // don't return the Documents VM because shouldn't display anything.
      }
      if (status !== AccountStatus.Docs) {
        return of(null);
      }
      // reload to get the status
      const companyId = vms[0].envelope.companyId;
      this.companyService.getByKey(companyId);
    }
    return of(vms);
  }

  /**
   * Reload the document package, refreshing DocumentPackageService.documentPackage$
   * @returns Terminating observablethat makes a single request and emits docs when the server replies.
   */
  loadDocumentPackage(): Observable<DocumentPackage> {
    return this.documentPackageService.loadDocumentPackage();
  }

  /**
   * Save EnvelopeSigner entity; no other saves needed because all other changes happen on the server via docusign
   * @returns Terminating observable emitting the EnvelopSigner after the save succeeds
   */
  save(envelopeSigner: EnvelopeSigner): Observable<EnvelopeSigner> {
    return this.envelopeSignerService.upsert(envelopeSigner);
  }

  /**
   * Tell DocuSign to create an Envelope and optionally send an email to signer and spouse.
   * @returns Terminating observable with envelope and DocuSign's status (e.g., 'sent' if the email was sent).
   */
  sendEnvelope(envelopeId: string, signers: EnvelopeSigner[]): Observable<EsignEnvelopeResponse> {
    return this.esignService.postEnvelope(envelopeId, signers);
  }
  // sendEnvelope(vm: DocumentsVm): Observable<EsignEnvelopeResponse> {
  //   return this.esignService.getEnvelope(vm.envelope.id, vm.signerEmail, vm.signerFirstName, vm.signerLastName,
  //     vm.signerTitle, vm.spouseEmail, vm.spouseFirstName, vm.spouseLastName);
  // }

  gotoDashboard() {
    this.sessionService.setAccountStatus(AccountStatus.Open).subscribe(
      _ => this.router.navigate([WellKnownRoutes.Dashboard])
      // TODO: Something with error
    );
  }

}
