import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Ancillary, CompanyAncillary } from '@model';
import { ChangeSetAdd } from 'ngrx-data';
import { changeItemFactory as cif } from '@utils';
import { CompanyAncillaryService, AncillaryService } from '@entity';
import { map, first, concatMap } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { IdGeneratorService } from '@app/core';
import { environment } from '@environment';

@Injectable({
  providedIn: 'root'
})
export class AncillaryHelperService {
  companyAncillaryVm$: Observable<CompanyAncillary[]>;
  anc$: Observable<Ancillary[]>;
  loadedAncillaryData = false;

  constructor(private idGeneratorService: IdGeneratorService,
    private companyAncillaryService: CompanyAncillaryService,
    private ancillaryService: AncillaryService,
    private http: HttpClient) {
      this.companyAncillaryVm$ = this.companyAncillaryService.entities$;
    }

  // #region TODO: Refactor to pattern
  getAncillaryData(): Observable<Ancillary[]> {
      return this.ancillaryService.loaded$.pipe(first(), concatMap(vm1 => {
        this.loadedAncillaryData = vm1;
        if (this.loadedAncillaryData === true) {
          return this.ancillaryService.entities$.pipe(map( col => this.updateAncillaryWithHtml(col) ));
        } else {
          return this.ancillaryService.load().pipe(map( col => this.updateAncillaryWithHtml(col) ));        }
      }));
    }

  updateAncillaryWithHtml(col: Ancillary[]): Ancillary[] {
    // Security: anc.Content is always authored by the documentation team
    // and is considered to be safe.
    for (const anc of col) {
      this.http.get('/assets/ancillary/' + anc.content, { headers: new HttpHeaders({
        'Accept':  'text/html'
      }), responseType: 'text' }).subscribe(data => { anc.htmlVal = data; });
    }
    return col;
  }
  // #endregion TODO: Refactor to pattern

  saveCompanyAncillary(ancillaryIds:  string[], companyId: string) {
    const sUrl = environment.apiRoot + '/save/companyancillary';
    if ( ancillaryIds.length > 0) {
      const changes: ChangeSetAdd<CompanyAncillary>[] = [];
      for (const ancId of ancillaryIds) {

        const ancillary: CompanyAncillary = new CompanyAncillary();
        ancillary.id = this.idGeneratorService.generateId();
        ancillary.companyId = companyId;
        ancillary.ancillaryId = ancId;

        changes.push(cif.add('CompanyAncillary', ancillary));
      }
    this.companyAncillaryService.saveEntities({ changes }, sUrl).pipe(
      map(_ => {})
    );
    }
  }
}
